.about{
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;

    .container{
        overflow: hidden;
        padding: 10px;
        box-sizing: border-box;

        margin-top: 50px;

        .title{
            margin-bottom: 30px;
        }
    }

    .block-wrapper{
        margin-top: 20px;

        width: 100%;
        display: grid;

        grid-template-columns: 1fr 1fr;
        grid-gap: 30px;
    }


    .block-item-wrapper{
        background-color: #f5f5f5;
        border-radius: 8px;

        &:nth-of-type(1){
            .block-item{
                transform: rotate(3deg);
            }
        }
        
        &:nth-of-type(2){
            .block-item{
                transform: rotate(-3deg);
            }
        }

        &:nth-of-type(3){
            .block-item{
                transform: rotate(-1deg);
            }
        }

        &:nth-of-type(4){
            .block-item{
                transform: rotate(2deg);
            }
        }
    }

    .block-item{
        height: 100%;
        
        cursor: pointer;

        border: 1px solid #eaeaea;
        border-radius: 8px;

        display: flex;
        flex-direction: column;

        overflow: hidden;

        transition: all 0.2s ease;

        &:hover{
            transform: rotate(0deg) !important;
        }


        &-quote{
            background-color: #fff;
            flex-grow: 1;

            display: flex;

            align-items: center;
            justify-content: center;

            .text{
                margin-top: 50px;
                margin-bottom: 30px;
                width: 80%;
                text-align: center;

                font-size: 19px;
                font-weight: 600;

                color:#3d3d3d;

                span{
                    color: #000;
                    font-size: 30px;
                }
            }
        }

        &-profile{
            height: 95px;
            background-color: #a855f7;

            display: flex;

            align-items: center;

            justify-content: flex-start;

            padding: 0 20px;

            img{
                width: 52px;
                height: 52px;

                border: 4px solid #fff;

                border-radius: 65px;
            }

            .info{
                margin-left: 12px;

                .name{
                    font-size: 17px;
                    color: #fff;

                    font-weight: 600;
                }

                .status{
                    font-size: 17px;
                    color: rgba(255, 255, 255, 0.8);

                    font-weight: 600;
                }
            }
        }
    }
}