@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;700&display=swap');

@import './glitch.scss';


.header-wrapper{
    width: 100%;
}


.sub-tagname{
    color: #fff;
    justify-self: flex-end;
    z-index: 21;

    margin-bottom: 20px;
    
    span{
        opacity: 0.3;
        text-transform: uppercase;
        display: block;
        font-weight: bold;
        font-family: 'Roboto Mono', monospace;
    }
}

.sub-header{
    min-width: 1200px;
    width: 100%;
    overflow: hidden;
    font-weight: bold;
    font-size: 60px;
    margin-top: -35px;

    letter-spacing: -5px;
    text-align: center;
    
    display: flex;
    justify-content: center;

    span{
        
    }
}

.header{
    position: relative;
    min-width: 1200px;
    width: 100%;
    height: 565px;
    
    background-color: #0a090c;


    display: flex;
    flex-direction: column;

    justify-content: center;
    align-items: center;

    position: relative;

    video{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        object-fit: cover;

        z-index: 0;

        opacity: 0.3;
    }

    .tagName{
        margin-bottom: -96px;
        z-index: 11;
        font-size: 20px;

        color: #fff;
        margin-top: 10px;

        font-family: 'Raleway', sans-serif;
        font-weight: 300;
    }

    .logo{
        font-size: 44px;
        letter-spacing: .5em;
        display: inline-block;
        font-family: 'Raleway', sans-serif;
        font-weight: bold;
        font-size: 64px;
        white-space: nowrap;
        z-index: 10;

        span{
            color: #fff;
        }

        span:nth-child(2){

        }
    }
}