.companies{
    border-top: 1px solid #eaeaea;
    border-bottom: 1px solid #eaeaea;
    min-width: 1200px;
    padding-top: 50px;
    margin-top: 100px;

    width: 100%;
    background-color: #fafafa;

    display: flex;
    justify-content: center;

    .title{
        text-transform: capitalize;
        font-size: 38px;
        font-weight: bold;

        margin-bottom: 45px;
    }

    .sub-title{
        text-align: center;
        margin-top: 40px;
        margin-bottom: 40px;

        font-size: 18px;

        color: rgb(145, 145, 145);
    }

    .inner-wrapper{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;

        & > * {
            cursor: pointer;

            padding: 0 10px;
            display: flex;
            justify-content: center;
            align-items: center;
            opacity: 0.5;

            transition: all 0.3s ease;
        }

        & > *:hover{
            opacity: 1;
        }
    }
}