.team{
    width: 100%;
    margin-top: 100px;

    .img-wrapper{
        overflow: hidden;
    }

    .text{
        width: 78%;
    }

    &.container{
        padding-bottom: 50px;
        padding: 10px 10px 50px 10px;
        box-sizing: border-box;
    }
    
    &.container:nth-child(1){
        border-bottom: 1px solid #dadada;
    }

    &.container:nth-child(2){
        margin-top: 50px;
    }

    .tagline{
        margin: 0px 0 60px 0;
    }

    .button{
        cursor: pointer;
        margin: 50px 0 0 0;

        transition: all 0.3s linear;

        &:hover{
            background: #383ee2;
        }
    }
    
    .fake-selector{
        z-index: -1;

        top: 0;
        left: 0;
        position: absolute;
        background-position: center bottom;
        background-size: cover;
        background-repeat: no-repeat;
        width: 100%;
        height: calc(100% - 69px);
        background-image: url("https://thecinemaholic.com/wp-content/uploads/2019/07/silicon-valley2.jpg");

        .inner{
            width: 100%;
            height: 100%;
        
            background-size: contain;
            background-position: center;
            background-repeat: no-repeat;
            background-color: #fff;
            //background: radial-gradient(rgba(221,219,220,1) 0%, rgba(255,255,255,1) 70%);
            display: flex;
            flex-direction: column;
            //align-items: center;

            span{
                background-size: cover;
                background: #396afc;  /* fallback for old browsers */
                background: -webkit-linear-gradient(to right, #2948ff, #396afc);  /* Chrome 10-25, Safari 5.1-6 */
                background: linear-gradient(to right, #2948ff, #396afc); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */


                -webkit-background-clip: text; 
                -webkit-text-fill-color: transparent; 
                -webkit-box-decoration-break: clone; 
                //color: #6967ec;
                
                line-height: 0.8;
                font-size: 90px;
                font-weight: bold;

            }

            span:nth-child(2){
                display: none;
                font-size: 80px;
                margin-top: 20px;
                opacity: 0.4;
            }
        }
    }

    .selector{
        position: relative;
        overflow: hidden;
    }

    .selector-wrapper{
        display: flex;
    }

    .selector-title{
        margin: 0px 0 50px 0;
    }

    .selector-item{
        pointer-events: none;
        
        font-family: 'Roboto Mono', monospace;

        display: flex;
        flex-direction: column;

        align-items: center;

        justify-content: flex-end;

        &:nth-child(1){
            margin-right: -141px;
        }

        &:nth-child(2){
            z-index: 10;
        }

        &:nth-child(3){
            z-index: 8;
            margin-left: -158px;
        }

        &:nth-child(4){
            margin-left: -118px;
        }        

        &.medium{
            //margin: 0 -65px;

            img{
                width: 350px;
            }
        }

        &.small{
            //margin: 0 -35px;
            
            img{
                width: 308px;
            }
        }
        
        img{
            width: 411px;
            height: 445px;
            object-fit: contain;
            object-position: center bottom;
        }

        &__info{
            margin-top: 10px;

            display: flex;
            flex-direction: column;

            align-items: center;

            &-wrapper{
                overflow: hidden;
            }

            .name{
                font-family: 'Roboto Mono', monospace;
                font-weight: bold;

                font-size: 24px;
            }

            .contacts{
                color: #27AE60;

                font-family: 'Roboto Mono', monospace;
                font-weight: bold;

                font-size: 18px;

                .tag{
                    cursor: pointer;
                    transition: all 0.3s linear;


                    &:hover{
                        opacity: 0.85;
                    }
                }

                & > * {
                    display: inline-block;
                }
            }
        }
    }
}