footer{
    width: 100%;
    background-color: #F8F8F8;
    height: 155px;

    border-top: 1px solid #dadada;

    flex-grow: 1;
    min-width: 1200px;

    display: flex;
    align-items: center;
    justify-content: center;

    .footer-wrapper{
        width: 1100px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .name{
        color: #525252;
        font-size: 1.12rem;
        font-weight: bold;
        margin-bottom: 11px;
    }

    .label{
        color: #7b7b7b;
        font-size: 1rem;
        margin-bottom: 8px;
        display: block;
        text-decoration: none;
        font-style: normal; 
    }
}