@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;400;700&family=Roboto:wght@400;700&display=swap');

*{
    padding: 0;
    margin: 0;
    font-family: 'Roboto', sans-serif;
}

html{
    scroll-behavior: smooth;
}

html, body, #root{
    height: 100%;
}

.App{
    display: flex;

    flex-direction: column;

    flex-wrap: wrap;

    & > *{
        flex-wrap: wrap;
    }
}

.big-title{
    font-size: 54px;
    font-weight: bold;
    
    line-height: 96%;
    letter-spacing: -6.5%;

    color: #000;


    &.left{
        align-self: flex-start;
    }

    &.center{
        text-align: center;
    }
}

.text{
    font-size: 20px;
    text-align: center;
    line-height: 1.5;
    color: rgba(107,114,128, 1);

    &.left{
        align-self: flex-start;
        text-align: left;
    }
}

.button{
    background: #3D44FB;

    display: inline-block;
    color: #fff;
    font-family: 'Roboto Mono', monospace;

    font-size: 16px;

    padding: 14px 30px;
}

.header-title{
    font-size: 36px;
    font-weight: bold;
    text-align: center;

    text-transform: capitalize;
}

.block-title{
    font-size: 44px;
    font-weight: bold;

    align-self: flex-start;
}

.header-title-sub{
    font-size: 15px;
    margin-top: 4px;
    color: rgb(80, 80, 80);
    text-align: center;

    font-family: 'Roboto Mono', monospace;
}

.container{
    min-width: 1200px;
    max-width: 1200px;
    display: flex;
    flex-direction: column;

    align-items: center;

    flex-wrap: wrap;
}

.codeline{
    margin-bottom: 15px;
    color: #9e9e9e;
    align-self: flex-start;
    font-family: 'Roboto Mono', monospace;
}